/****************** FEED BACK ******************/ 
.feedback {
    padding-top:  100px;
}
.swiper {
    max-width:50%!important; 
    display: flex;
    justify-content: flex-end;
    margin-right: unset;
    margin-bottom:150px;
    @include tablette {
        padding:0;
        margin:0 $pad2;
        justify-content: center;
    }
}


.swiper-slide {
    width:100%!important; 
    padding: 0 4rem;
    @include tablette {
        width:100%;
    }
    p {
        font-size: $pad1;
    }
    .feedback__subtitle {
        display: flex;
        justify-content: space-between;
        margin: calc($pad1 / 2) 0;
        .feedback__star {
            i {
                color: $pink;
                padding:0 5px;
            }
            i:nth-of-type(-n+3) {
                color: $dark_pink;
            }
        }
    }
}

/* prev / next */


.swiper-button-next, .swiper-button-prev {
    color: $white;
}

.swiper-button-prev:after, .swiper-button-next:after {
    font-size: 50px;
    font-weight: 900;
    text-shadow: $ombre001;
}



